import {Route} from 'backbone-routing';
import View from './view';
import nprogress from 'nprogress';
import ordersStorage from '../storage';
import nav from '../../nav/nav';
import user from '../../profile/model';
import $ from 'jquery';

export default Route.extend({
  initialize(options = {}) {
    this.layout = options.layout;
  },

  fetch() {
    nprogress.start();

    return user.fetch().then(model => {
      this.user = model;
      return ordersStorage.findAll().then(collection => {
        nprogress.done();
        this.collection = collection;
      });
    });
  },

  onFetch(id) {
    this.model = this.collection.get(id);
  },

  render() {
    nav.do(`<a href="#orders"></a>
      <i class="fa fa-angle-right"></i>
      #${this.model.get('id')} ${this.model.get('title')}`);

    this.view = new View({
      model: this.model,
      user: this.user
    });

    this.layout.orders.show(this.view);

    let userId = this.user.get('id');
    let candidates = this.model.get('candidates');

    if (candidates.indexOf(userId) === -1 ) {
      $('.do-accept-wrap').show();
    }
  }

});
