import {Collection} from 'backbone';
import Model from './model';
import Api from '../api/api';

export default Collection.extend({
  model: Model,
  fetch() {
    return Api.getMyOrders().then(resp => {
      if (resp.success) {
        this.set(resp.orders);
        this.trigger('sync');
        return Promise.resolve(this);
      }
    });
  }
});
