import {Route} from 'backbone-routing';
import View from './view';
import user from '../model';
import nprogress from 'nprogress';
import $ from 'jquery';

export default Route.extend({
  initialize(options = {}) {
    this.container = options.container;
  },

  fetch() {
    nprogress.set(0.5);
    return user.fetch().then(model => {
      this.model = model;
      nprogress.done();
    });
  },

  initVals() {
    function range(i){return i?range(i-1).concat(i):[];}

    $('.my-profile-card__img img').on('error', () => {
      $('.my-profile-card__img img').attr('src', '/img/no-photo.png');
    });

    let years;

    range(99).forEach(num => {
      if (num > 29) {
        years += `<option>19${num}</option>`;
      }
    });

    $('#select-year').html(years);
  },

  render() {
    this.view = new View();
    this.view.model = this.model;
    this.container.show(this.view);

    this.initVals();
  }
});
