import {ItemView} from 'backbone.marionette';
import template from './template.hbs';
import Cookies from 'js-cookie';
import {history} from 'backbone';
import Api from '../api/api';
import nprogress from 'nprogress';
import user from '../profile/model';
import $ from 'jquery';
import 'jquery.inputmask';

export default ItemView.extend({
  template: template,
  className: 'login',

  events: {
    'submit #login__form' : 'handleLogin',
  },

  onShow() {
    $('#form-phone').inputmask('+7 (999) 999-99-99');
  },

  handleLogin() {
    nprogress.start();
    $('.login__submit').attr('disabled', 'disabled');

    if ($('.login__submit').data().phase === 1) {
      this.loginPhone();
    }

    if ($('.login__submit').data().phase === 2) {
      this.loginCode();
    }

    return false;
  },
  loginPhone() {
    Api.sms($('#form-phone').val(), (json) => {
      if (!json.success) {
        this.handleError(json.message);
      } else {
        $('#form-phone').attr('disabled', 'disabled');
        $('#form-code-section').slideDown(200);
        $('.login__submit')
          .removeAttr('disabled')
          .text('')
          .data('phase', 2);
        $('#infobox').slideUp(200);
        $('#form-code').focus();
        nprogress.done();
      }
    });
  },
  loginCode() {
    let
      phone = $('#form-phone').val(),
      code = $('#form-code').val();


    Api.login(phone, code, json => {
      if (!json.success) {
        this.handleError(json.message);
      } else {
        Cookies.set('userToken', json.token);
        user.fetched = false;
        user.fetch().then(() => {
          history.navigate('orders', { trigger: true });
        });
      }
    });
  },
  handleError(message) {
    $('#infobox')
      .slideDown(200)
      .text(message);
      $('.login__submit').removeAttr('disabled');
      nprogress.done();
  }
});
