import {Router} from 'backbone-routing';
import ProfileRoute from './route';
import ProfileEditRoute from './edit/route';
import PayRoute from './pay/route';
import TypeRoute from './type/route';
import $ from 'jquery';
import {history} from 'backbone';
import Cookies from 'js-cookie';

export default Router.extend({
  initialize(options = {}) {
    this.container = options.container;
  },

  onBeforeEnter() {
    $('.list-group-item').removeClass('active');
    $('#sidebar')
      .find(`[href='${location.hash}']`)
      .addClass('active');

      if (!Cookies.get('userToken')) {
        history.navigate('login', {
          trigger: true,
          replace: true
        });
        return false;
      }
  },

  routes: {
    'profile': 'index',
    'profile/edit': 'edit',
    'profile/balance/pay': 'pay',
    'profile/type': 'type',
  },

  index() {
    return new ProfileRoute({
      container: this.container
    });
  },

  edit() {
    return new ProfileEditRoute({
      container: this.container
    });
  },

  pay() {
    return new PayRoute({
      container: this.container
    });
  },

  type() {
    return new TypeRoute({
      container: this.container
    });
  },

});
