import Cookies from 'js-cookie'
import $ from 'jquery'

export default {
  baseUrl: 'https://shipment.herokuapp.com/api/v2',
  // baseUrl: 'http://localhost:8080/api/v2',

  token () {
    let token = Cookies.get('userToken')
    if (token) {
      return token
    } else {
      Cookies.remove('userToken')
      window.location.replace('/#login')
      return false
    }
  },

  sms (phone, success) {
    phone = phone.replace(/[^0-9]/g, '')
    $.ajax({
      url: `${this.baseUrl}/sms_code/`,
      data: { phone: phone },
      success: success
    })
  },

  login (phone, code, success) {
    phone = phone.replace(/[^0-9]/g, '')
    $.ajax({
      url: `${this.baseUrl}/token/`,
      data: { phone: phone, code: code },
      method: 'get',
      success: success
    })
  },

  getProfile () {
    return window.fetch(`${this.baseUrl}/user/?token=${this.token()}`)
      .then(response => {
        return response.json()
      })
  },

  getUser (id) {
    return window.fetch(`${this.baseUrl}/user/${id}?token=${this.token()}`)
      .then(response => {
        return response.json()
      })
  },

  editProfile (data) {
    data.token = this.token()
    var $ajax = $.post(`${this.baseUrl}/user/`, data)
    return Promise.resolve($ajax)
  },

  upload (formData) {
    formData.append('token', this.token())
    var $ajax = $.ajax({
      url: `${this.baseUrl}/user_file/`,
      type: 'post',
      data: formData,
      cache: false,
      contentType: false,
      processData: false
    })
    return Promise.resolve($ajax)
  },

  getCars () {
    return window.fetch(`${this.baseUrl}/vehicles/?token=${this.token()}`)
      .then(response => {
        return response.json()
      })
  },

  addCar (data) {
    data.token = this.token()

    var $ajax = $.post(`${this.baseUrl}/vehicles/`, data)
    return Promise.resolve($ajax)
  },

  editCar (data) {
    data.token = this.token()

    var $ajax = $.post(`${this.baseUrl}/vehicles/${data.id}`, data)
    return Promise.resolve($ajax)
  },

  deleteCar (data) {
    data.token = this.token()
    var $ajax = $.post(`${this.baseUrl}/vehicles/${data.id}/delete/`, data)
    return Promise.resolve($ajax)
  },

  takeCar (id) {
    let data = {
      token: this.token()
    }
    var $ajax = $.post(`${this.baseUrl}/vehicles/${id}/take/`, data)
    return Promise.resolve($ajax)
  },

  getOrders () {
    return window.fetch(`${this.baseUrl}/orders/?token=${this.token()}`)
      .then(response => {
        return response.json()
      })
  },

  getOrdersNumber () {
    return window.fetch(`${this.baseUrl}/orders/number?token=${this.token()}`)
      .then(response => {
        return response.json()
      })
  },

  getMyOrders () {
    return window.fetch(`${this.baseUrl}/orders/my?token=${this.token()}`)
      .then(response => {
        return response.json()
      })
  },

  addOrder (data) {
    data.token = this.token()

    var $ajax = $.post(`${this.baseUrl}/orders/`, data)
    return Promise.resolve($ajax)
  },

  acceptOrder (id) {
    let data = {
      token: this.token()
    }

    var $ajax = $.post(`${this.baseUrl}/orders/${id}/accept`, data)
    return Promise.resolve($ajax)
  },

  doneOrder (id) {
    let data = {
      token: this.token()
    }

    var $ajax = $.post(`${this.baseUrl}/orders/${id}/done`, data)
    return Promise.resolve($ajax)
  },

  cancelOrder (id) {
    let data = {
      token: this.token()
    }

    var $ajax = $.post(`${this.baseUrl}/orders/${id}/cancel`, data)
    return Promise.resolve($ajax)
  },

  confirmOrderCandidate (id, candidate_id) {
    let data = {
      candidate_id: candidate_id,
      token: this.token()
    }

    var $ajax = $.post(`${this.baseUrl}/orders/${id}/confirm`, data)
    return Promise.resolve($ajax)
  },

  getTemplates () {
    return window.fetch(`${this.baseUrl}/templates/?token=${this.token()}`)
      .then(response => {
        return response.json()
      })
  },

  saveTemplate (id) {
    let data = {
      token: this.token()
    }

    var $ajax = $.post(`${this.baseUrl}/orders/${id}/save`, data)
    return Promise.resolve($ajax)
  },

  removeTemplate (id) {
    let data = {
      token: this.token()
    }

    var $ajax = $.post(`${this.baseUrl}/orders/${id}/save_cancel`, data)
    return Promise.resolve($ajax)
  },

  getCompanies () {
    return window.fetch(`${this.baseUrl}/companies/?token=${this.token()}`)
      .then(response => {
        return response.json()
      })
  },

  addCompany (data) {
    data.token = this.token()

    var $ajax = $.post(`${this.baseUrl}/company/`, data)
    return Promise.resolve($ajax)
  },

  deleteCompany (id) {
    let $ajax = $.post(
      `${this.baseUrl}/company/${id}/delete`,
      {token: this.token()}
    )
    return Promise.resolve($ajax)
  },

  editCompany (data) {
    data.token = this.token()

    var $ajax = $.post(`${this.baseUrl}/company/${data.id}`, data)
    return Promise.resolve($ajax)
  },

  getCompanyUsers (id) {
    return window.fetch(`${this.baseUrl}/company/${id}/users?token=${this.token()}`)
      .then(response => {
        return response.json()
      })
  }

}
