import {Collection} from 'backbone';
import Model from './model';
import Api from '../api/api';

export default Collection.extend({

  model: Model,

  fetch() {
    return Api.getCompanies().then(resp => {
      if (resp.success) {
        this.set(resp.companies);
        this.trigger('sync');
        return Promise.resolve(this);
      }
    });
  },

});
