import {Route} from 'backbone-routing';
import View from './view';
import $ from 'jquery';
import nprogress from 'nprogress';
import storage from '../storage';
import nav from '../../nav/nav';

export default Route.extend({
  initialize(options = {}) {
    this.layout = options.layout;
  },

  fetch() {
    nprogress.start();
    return storage.findAll().then(collection => {
      nprogress.done();
      this.collection = collection;
    });
  },

  onFetch(id) {
    this.model = this.collection.get(id);
    this.model.getUsers().then(resp => {
      resp.users.forEach(person => {
        let crown = `<img
          style="position: absolute; top: 36px; left: 5px; width: 30px;"
          src="/img/crown.png" alt="" />`;
        if (person.id !== this.model.get('admin_user_id')) {
          crown = '';
        }
        let html = `
          <a href="#users/${person.id}" class="company__person">
            ${crown}
            <div class="company__person__img">
              <img src="${person.user_pic}" alt="">
            </div>
            <div class="company__person__name">
              ${person.firstname}<br>${person.middlename}
            </div>
          </a>
          `;
        $('.company__people').append(html);
      });
      $('#company-count').append(`
         :
        <b>${resp.users.length}</b>
      `);
    });
  },

  render() {
    this.view = new View({
      model: this.model
    });
    this.layout.companies.show(this.view);
    nav.do(this.model.get('name'));
  }

});
