import {ItemView} from 'backbone.marionette';
import template from './template.hbs';
import $ from 'jquery';
import moment from 'moment';

export default ItemView.extend({
  template: template,
  tagName: 'div',
  onShow() {
    moment.locale('ru');
    var time = moment.unix( this.model.get('timestamp') );
    $('#order-date').text(time.format('YYYY-MM-DD'));
    $('#order-time').text(time.format('HH:mm'));
  }
});
