import $ from 'jquery'
import {history} from 'backbone'
import Api from '../api/api'

export default {

  do (header) {

    this.cleanSelections()

    $('.list-group-item').removeClass('active')

    $('#sidebar')
      .find(`[href='${window.location.hash.match(/[#a-z]+/)[0]}']`)
      .addClass('active')

    let src = $('.list-group-item.active img').attr('src')
    src = src.replace('.svg', '-active.svg')
    $('.list-group-item.active img').attr('src', src)

    $('#screen-header').html(header)
  },

  cleanSelections () {
    $('.list-group-item').each((a, item) => {
      let src = $(item).find('img').attr('src')
      src = src.replace('-active', '')
      $(item).find('img').attr('src', src)
    })
  },

  init (user) {
    this.user = user

    if (!this.checkUser()) {
      return false
    }

    this.renderHeader()
    this.refreshOrdersNumber()
  },

  checkUser () {
    if ($.isEmptyObject(this.user.attributes)) {
      history.navigate('login', {trigger: true})
      $('#sidebar').hide(200)
      return false
    }

    if (this.user.get('type_of_account') === null) {
      history.navigate('profile/type', {trigger: true})
      $('#sidebar').hide(200)
    } else {
      $('#sidebar').show(200)
    }

    this.checkUserType()

    return true
  },

  checkUserType () {
    if (this.user.isDriver()) {
      $('.menu-item-cars').show()
      if (this.user.get('current_car') === null) {
        $('#cars-danger').show(200)
        $('.menu-item-cars').attr('href', '#cars/add')
      }
    } else {
      $('.sidebar-new-order').show()
      $('.menu-item-templates').show()
    }
  },

  refreshOrdersNumber () {
    Api.getOrdersNumber()
    .then(resp => {
      if (resp.success && resp.number > 0) {
        $('#orders-number')
          .show(300)
          .text(resp.number)
      }
    })
  },

  renderHeader () {
    if (!this.user.get('firstname') && !this.user.get('lastname')) {
      return false
    }

    let fullname = `${this.user.get('firstname')} ${this.user.get('lastname')}`

    $('.header-profile__name__val').html(`${fullname}
      <i style="opacity:.5" class="fa fa-caret-down"></i>`)
    $('.header-profile__balance').html(`:
      ${this.user.get('balance')}
      .`)
    $('.header-profile__name__img')
      .html(`<img src="${this.user.get('user_pic')}">`)
    $('.header-profile').show()
  }

}
