import {CompositeView} from 'backbone.marionette'
import OrderView from './order-view'
import template from './wrapper-template.hbs'
import $ from 'jquery'

export default CompositeView.extend({
  childView: OrderView,
  template: template,
  childViewContainer: 'tbody',

  events: {
    'keyup .orders-search-filter': 'searchFilter',
    'change .orders-type-filter': 'typeFilter',
    'change .orders-time-filter': 'timeFilter'
  },

  viewComparator (model) {
    return model.get('status') !== 2
  },

  active: '',

  filters: {
    search: '',
    type: -1,
    time: -1
  },

  filter (model) {
    let search = this.filters.search.toLowerCase()
    let type = this.filters.type

    if (search !== '') {
      if (model.strings().indexOf(search) === -1) {
        return false
      }
    }

    if (type !== -1) {
      if (+type !== +model.get('status')) {
        return false
      }
    }

    return true
  },

  searchFilter (e) {
    this.filters.search = $(e.currentTarget).val()
    this.active = '.orders-search-filter'
    this.render()
  },

  typeFilter (e) {
    this.filters.type = +$(e.currentTarget).val()
    this.active = '.orders-type-filter'
    this.render()
  },

  timeFilter (e) {
    this.filters.time = +$(e.currentTarget).val()
    this.active = '.orders-time-filter'
    this.render()
  },

  initialize (options = {}) {
    this.collection = options.collection
    this.user = options.user
  },

  templateHelpers () {
    let driver = this.user.isDriver()

    return {
      driver: driver
    }
  },

  onRender () {
    this.$el.find('.orders-search-filter').val(this.filters.search)

    this.$el.find('.orders-type-filter').val(this.filters.type)

    if (this.active) {
      this.$el.find(this.active).focus()
    }
  }

})
