import {Model} from 'backbone'
import Api from '../api/api'

export default Model.extend({

  validate (attrs = {}) {
    let errors = []

    if (attrs.manufacturer === '') {
      errors.push('  ')
    }

    if (attrs.number === '') {
      errors.push('  ')
    }

    return errors.length > 0 ? errors : undefined
  },

  sync (method, model) {
    if (method === 'create') {
      return Api.addCar(model.toJSON()).then(resp => {
        this.set('id', resp.id)
      })
    }
    if (method === 'update') {
      return Api.editCar(model.toJSON())
    }
    if (method === 'delete') {
      return Api.deleteCar(model.toJSON())
    }
  },

  getVolume () {
    let volume =
      this.get('body_size_x') *
      this.get('body_size_y') *
      this.get('body_size_z')
    return volume
  },

  getBodyTypeWord () {
    let bodyTypes = [
      '',
      '',
      '',
      ''
    ]

    return bodyTypes[this.get('body_type')]
  },

  defaults: {
    type: '0',
    body_type: '1',
    pass_num: 1,
    pass_cartype: 0
  }

})
