import {ItemView} from 'backbone.marionette'
import template from './model-template.hbs'
import Api from '../../api/api'
import nprogress from 'nprogress'

export default ItemView.extend({
  template: template,

  events: {
    'click .vehicle-card__take > a': 'take'
  },

  modelEvents: {
    'change': 'change'
  },

  change () {
    this.render()
  },

  take (e) {
    e.preventDefault()
    let id = this.model.get('id')
    nprogress.start()
    Api.takeCar(id).then(resp => {
      nprogress.done()
      if (resp.success) {
        this.model.set('current', true)
      } else {
        window.alert(resp.message)
      }
    })
  },

  getLoadType (loadCapacity) {
    if (loadCapacity < 1) {
      return ''
    }
    if (loadCapacity > 1) {
      return ''
    }
    if (loadCapacity > 5) {
      return ''
    }
  },

  templateHelpers () {
    let firstpic = this.model.get('pics')

    if (firstpic) {
      firstpic = JSON.parse(firstpic)[0]
    } else {
      firstpic = null
    }

    let loadType = this.getLoadType(this.model.get('load_capacity'))

    return {
      firstpic: firstpic,
      'body_type_word': this.model.getBodyTypeWord(),
      'body_volume': this.model.getVolume(),
      'loadType': loadType
    }
  }

})
