import {ItemView} from 'backbone.marionette'
import template from './order-template.hbs'

export default ItemView.extend({
  template: template,
  tagName: 'a',

  templateHelpers () {
    let firstpic = this.model.get('pics')
    let letter

    if (this.model.get('name')) {
      letter = this.model.get('name')[0].toUpperCase()
    }

    return {
      firstpic,
      letter
    }
  },

  attributes () {
    return {
      href: `#companies/${this.model.get('id')}`
    }
  }

})
