import {CompositeView} from 'backbone.marionette'
import template from './layout-template.hbs'
import OrderView from './order-view'
import nav from '../../nav/nav'
import $ from 'jquery'

export default CompositeView.extend({

  childViewContainer: '.companies-wrap',
  childView: OrderView,
  template: template,

  collectionEvents: {
    'add': 'modelAdded'
  },

  modelAdded () {
    this.render()
  },

  viewComparator (candidate) {
    return !candidate.get('is_owned_by_user')
  },

  onShow () {
    nav.do('')
    if (this.isOwner()) {
      $('#add-button')
        .text('  ')
        .data().action = 'invite'
    }
  },

  isOwner () {
    let owner = false
    this.collection.toJSON().forEach(obj => {
      if (obj.is_owned_by_user) {
        owner = true
      }
    })
    return owner
  }

})
