import {Route} from 'backbone-routing';
import {Collection} from 'backbone';
import View from './view';
import nprogress from 'nprogress';
import ordersStorage from '../storage';
import user from '../../profile/model';

export default Route.extend({

  initialize(options = {}) {
    this.layout = options.layout;
    this.user = {};
  },

  fetch() {
    nprogress.start();

    return user.fetch().then(model => {
      this.user = model;
      return ordersStorage.findAll().then(collection => {
        nprogress.done();
        this.collection = collection;
      });

    });
  },


  onFetch(id) {
    this.model = this.collection.get(id);

    this.model.get('candidates_data').map((candidate) => {
      candidate.price = this.model.countPrice(candidate.current_car_data);
      return candidate;
    });
  },

  render() {
    this.view = new View({
      collection: new Collection(this.model.get('candidates_data')),
      user: this.user,
      model: this.model
    });
    this.layout.orders.show(this.view);
  },
});
