import moment from 'moment';

export default {
  numberWithSpaces(number) {
    if (!number) {
      return false;
    }
    return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ' ');
  },

  dateTimeBr(ts) {
    let time = moment.unix(ts);
    return `
      <nobr>${time.format('DD MMMM YYYY')}</nobr>
      <br> ${time.format('HH:mm')}
    `;
  }
}
