import {ItemView} from 'backbone.marionette';
import template from './template.hbs';
import $ from 'jquery';
import Api from '../../api/api';
import {history} from 'backbone';
import nprogress from 'nprogress';
import user from '../model';
import nav from '../../nav/nav';


export default ItemView.extend({
  template: template,
  modelEvents: {
    all: 'render'
  },
  events: {
    'submit #userEdit__form' : 'edit',
    'change [name=pic]' : 'upload',
  },
  upload() {
    var formData = new FormData($('#userEdit__form'));
    var file = $('[name=pic]')[0].files[0];

    nprogress.start();
    formData.append('pic', file, 'file.jpg');

    Api
      .upload(formData)
      .then(resp => {
        nprogress.done();
        if (resp.success) {
          $('#user_pic').val(resp.uploaded_pic);
          $('#preview').attr('src', resp.uploaded_pic);
        }
      });
  },

  getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
  },

  edit: function (e) {
    e.preventDefault();

    var newModel = {};
    $('#userEdit__form').find('input').each(function (i, el) {
        if ($(el).val() !== '') {
            newModel[el.id] = $(el).val();
        }
    });

    Api
      .editProfile(newModel)
      .then(resp => {
        if (resp.success) {
          user.set(newModel);
          history.navigate('profile', {
            trigger: true,
            replace: true
          });
        }
      });
  },
  onShow() {
    nav.do(' ');
  }
});
