import {ItemView} from 'backbone.marionette'
import template from './template.hbs'
import nav from '../nav/nav'
import $ from 'jquery'
import PhoneFormatter from 'phone-formatter'

export default ItemView.extend({

  template: template,

  modelEvents: {
    all: 'render'
  },

  events: {
    'click .send-documents': 'sendDocuments',
    'click .close-wnd': 'sendDocumentsClose'
  },

  sendDocuments () {
    $('.order-success').fadeIn(200)
    $('.order-success-black').fadeIn(200)
  },

  sendDocumentsClose () {
    $('.order-success').fadeOut(200)
    $('.order-success-black').fadeOut(200)
  },

  onShow () {
    nav.do('')
  },

  templateHelpers () {
    let driver = this.model.isDriver()
    return {
      driver: driver,
      phoneFormatted: PhoneFormatter.format(this.model.get('phone'), '+7 (NNN) NNN-NN-NN')
    }
  }

})
