import {Route} from 'backbone-routing';
import Api from '../../api/api';
import Orders from '../collection';
import View from './view';
import $ from 'jquery';
import nprogress from 'nprogress';

export default Route.extend({
  initialize(options = {}) {
    this.layout = options.layout;
  },
  fetch() {
    nprogress.start();
    return Api.getOrders().then(resp => {
      nprogress.done();
      this.collection = new Orders(resp.orders);
    });
  },
  onFetch(id) {
    this.model = this.collection.get(id);
  },
  render() {
    $('#screen-header').html(`
      <h1 style="margin-top: 0" class="pull-left">
        <a href="#orders"></a>
        <i class="fa fa-angle-right"></i>
        #${this.model.get('id')}
      </h1>`);

    this.view = new View({
      model: this.model
    });
    this.layout.orders.show(this.view);

    $('.list-group-item').removeClass('active');
    $('#sidebar')
      .find(`[href='${location.hash.match(/[#a-z]+/)[0]}']`)
      .addClass('active');
    $('#screen-header').text('');
  }
});
