import {LayoutView} from 'backbone.marionette';
import template from './layout-template.hbs';
import $ from 'jquery';

export default LayoutView.extend({
  template: template,
  events: {
    'click #add-button': 'addCompany',
    'click .close-wnd': 'closeWnd',
  },

  addCompany(e) {
    if ($('#add-button').data('action') === 'invite') {
      e.preventDefault();
      $('.order-success').fadeIn(200);
      $('.order-success-black').fadeIn(200);
    }
  },

  closeWnd(e) {
    e.preventDefault();
    $('.order-success').fadeOut(200);
    $('.order-success-black').fadeOut(200);
  },

  regions: {
    companies: '#companies-container'
  }
});
