import {CompositeView} from 'backbone.marionette';
import {history} from 'backbone';
import template from './wrapper-template.hbs';
import ItemView from './item-view.js';
import $ from 'jquery';
import Api from '../../api/api';
import nav from '../../nav/nav';

export default CompositeView.extend({
  template: template,
  childView: ItemView,
  childViewContainer: '.driver-cards',

  events: {
    'click .driver-card__select': 'selectDriver',
    'click .sortby': 'reorder',
  },

  sorter: 'rating',

  viewComparator(candidate) {
    if (this.sorter === 'price') {
      return candidate.get(this.sorter);
    }

    return -candidate.get(this.sorter);
  },

  reorder(e) {
    if ($(e.target).hasClass('active')) {
      return;
    }

    this.sorter = $(e.target).data('sortby');

    this.render();
  },

  onRender() {
    setTimeout(()=>{
      if (this.sorter === 'price') {
        $(`[data-sortby=${this.sorter}]`)
          .addClass('active')
          .append('<i class="fa fa-sort-amount-asc"></i>');
      } else {
        $(`[data-sortby=${this.sorter}]`)
          .addClass('active')
          .append('<i class="fa fa-sort-amount-desc"></i>');
      }
    }, 50);
  },

  selectDriver(e) {
    let driver_id = $(e.currentTarget).data().driverId;
    Api.confirmOrderCandidate(this.model.get('id'), driver_id).then((resp)=> {
      if (resp.success) {
        history.navigate('orders', {trigger: true});
        location.reload();
      } else {
        window.alert(resp.message);
      }
    });
  },

  initialize(options = {}) {
    this.collection = options.collection;
    this.user = options.user;
  },

  onShow() {
    nav.do(`<a href="#orders"></a>
      <i class="fa fa-angle-right"></i>
      <a href="#orders/${this.model.get('id')}">#${this.model.get('id')}</a>
      <i class="fa fa-angle-right"></i>
      
      `);

  }
});
