import {CompositeView} from 'backbone.marionette'
import template from './collection-template.hbs'
import ModelView from './model-view'
import _ from 'lodash'

export default CompositeView.extend({
  template: template,
  childViewContainer: '#cars-container',
  childView: ModelView,

  viewComparator (model) {
    return !model.get('current')
  },

  collectionEvents: {
    'change': 'childChanged'
  },

  childChanged (child) {
    if (!child.get('current')) { return }
    let id = child.get('id')
    let currentCars = this.collection.where({current: true})
    _.each(currentCars, car => {
      if (car.get('id') !== id) {
        car.set('current', false)
      }
    })
    // console.log(prev);
  }
})
