import {Model} from 'backbone'
import Api from '../api/api'
import ordersStorage from '../orders/storage'

export default Model.extend({

  sync (method) {
    if (method === 'delete') {
      ordersStorage.find(this.get('id')).then(order => {
        order.set('is_template', false)
      })
      return Api.removeTemplate(this.get('id'))
    }
  }
})
