import {Route} from 'backbone-routing';
import Api from '../../api/api';
import View from './view';
import nprogress from 'nprogress';
import Model from '../model';

export default Route.extend({

  initialize(options = {}) {
    this.layout = options.layout;
    this.id = options.id;
  },

  fetch() {
    nprogress.start();
    return Api.getUser(this.id).then(resp => {
      nprogress.done();
      this.model = new Model(resp.user);
    });
  },

  render() {
    this.view = new View({
      model: this.model
    });
    this.layout.orders.show(this.view);
  }
});
