import {Model} from 'backbone'
import _ from 'lodash'
import Api from '../api/api'
import moment from 'moment'

export default Model.extend({

  initialize () {

    let cargo = this.get('cargo')

    if (typeof cargo === 'string') {
      this.set('cargo', JSON.parse(cargo))
    }

    this.cargoConfig()
  },

  strings () {
    let str = ''

    this.get('locations').forEach(loc => {
      str += [
        loc.address || '',
        loc.street || '',
        loc.city || '',
        loc.house || ''
      ].join(' ').toLowerCase()
    })

    return str
  },

  cargoConfig () {
    let cargo = this.get('cargo')
    _.map(cargo, (c) => {
      if (c.size_x && c.size_y && c.size_z) {
        c.sizes = true
      }
    })
  },

  defaults: {
    type: 0,
    datetime: Math.floor(Date.now() / 1000),
    pass_vehicle_type: '0',
    pass_passangers: 1,
    formdate: moment(Date.now()).format('D MMMM YYYY'),
    formtime: '14:00'
  },

  validate (attrs = {}) {
    let errors = []

    if (attrs.locations.length < 2) {
      errors.push(' ')
    }

    return errors.length > 0 ? errors : undefined
  },

  countPrice (car) {
    let price = 0
    let after2Hours = Number(car.tariff_after_2hours)
    let first2Hours = Number(car.tariff_first_2hours)

    if (this.get('ttk_entrance')) {
      price += Number(car.tariff_to_ttk)
    }
    if (this.get('center_entrance')) {
      price += Number(car.tariff_to_center)
    }

    price += after2Hours
    price += first2Hours

    return price
  },

  sync (method, model) {
    if (method === 'create') {
      return Api.addOrder(model.toJSON()).then(resp => {
        this.set('id', resp.id)
      })
    }
  },

  saveTemplate () {
    return Api.saveTemplate(this.get('id')).then(resp => {
      if (resp.success) {
        this.set('is_template', true)
      }
    })
  },
  removeTemplate () {
    return Api.removeTemplate(this.get('id')).then(resp => {
      if (resp.success) {
        this.set('is_template', false)
      }
    })
  }
})
