import {Collection} from 'backbone';
import Api from '../api/api';
import Model from './model';

export default Collection.extend({
  model: Model,
  fetch() {
    return Api.getTemplates().then(resp => {
      if (resp.success) {
        this.set(resp.templates);
        this.trigger('sync');
        return Promise.resolve(this);
      }
    });
  }
});
