import {Route} from 'backbone-routing'
import Model from '../model'
import View from './view'

export default Route.extend({
  initialize (options = {}) {
    this.layout = options.layout
  },

  fetch () {
    this.model = new Model({is_owned_by_user: true, members: 1})
  },

  render () {
    this.view = new View({
      model: this.model
    })

    this.layout.companies.show(this.view)
  }
})
