import {ItemView} from 'backbone.marionette'
import {history} from 'backbone'
import template from './order-template.hbs'
import user from '../../profile/model'
import Formatter from '../../tools/formatter'
import templatesStorage from '../../templates/storage'

export default ItemView.extend({
  template: template,
  tagName: 'tr',

  events: {
    'click .do-save-template': 'toggleTemplate',
    'click .do-show': 'show'
  },

  show () {
    let candidates = this.model.get('candidates')
    let status = this.model.get('status')
    let order_id = this.model.get('id')

    if (candidates.length && status === 1 && !user.isDriver()) {
      history.navigate(`orders/${order_id}/candidates`, {
        trigger: true
      })
    } else {
      history.navigate(`orders/${order_id}`, {
        trigger: true
      })
    }
  },

  toggleTemplate () {
    if (this.model.get('is_template')) {
      this.model.removeTemplate().then(() => {
        templatesStorage.records.fetch()
        this.render()
      })
    } else {
      this.model.saveTemplate().then(() => {
        templatesStorage.records.fetch()
        this.render()
      })
    }
  },

  getStatusText () {
    let status = this.model.get('status')
    let candidates = this.model.get('candidates')

    if (status === 2) {
      return `<span class="text-info"> </span>`
    }

    if (status === 1 &&
      candidates.length && user.get('type_of_account') === 0) {

      return `<span class="text-warning">
          :
        ${candidates.length}
      </span>`
    }
    if (status === 1 &&
      candidates.length && user.get('type_of_account') === 1) {
      return `<span class="text-warning">
         
      </span>`
    }

    if (status === 1) {
      return `<span class="text-warning"> </span>`
    }

    return `<span class="text-success"></span>`
  },

  countPrice (car) {
    let price = 0

    if (this.model.get('ttk_entrance')) {
      price += car.tariff_to_ttk
    }
    if (this.model.get('center_entrance')) {
      price += car.tariff_to_center
    }

    price += car.tariff_after_2hours + car.tariff_first_2hours

    return price
  },

  templateHelpers () {
    let price = false
    let assignee = this.model.get('assignee_data')

    if (assignee && assignee.current_car_data) {
      price = this.countPrice(assignee.current_car_data)
    }

    return {
      statusText: this.getStatusText(),
      driver: user.get('type_of_account') === 1,
      price: Formatter.numberWithSpaces(price),
      dateTimeFormatted: Formatter.dateTimeBr(this.model.get('datetime'))
    }
  }

})
