import {Model} from 'backbone'
import Api from '../api/api'

export default Model.extend({

  validate (attrs = {}) {
    let errors = []

    if (attrs.name === '') {
      errors.push('  ')
    }

    return errors.length > 0 ? errors : undefined
  },

  sync (method, model) {
    if (method === 'create') {
      return Api.addCompany(model.toJSON()).then(resp => {
        this.set('id', resp.id)
      })
    }
    if (method === 'update') {
      return Api.editCompany(model.toJSON())
    }
    if (method === 'delete') {
      return Api.deleteCompany(model.get('id'))
    }
  },

  getUsers () {
    return Api.getCompanyUsers(this.get('id'))
  }

})
