import {Router} from 'backbone-routing';
import IndexRoute from './index/route';
import ShowRoute from './show/route';
import AddRoute from './add/route';
import LayoutView from './layout-view';

import MyRoute from './my/route';
import MyShowRoute from './my-show/route';
import CandidatesRoute from './candidates/route';

export default Router.extend({

  initialize(options = {}) {
    this.container = options.container;
  },

  onBeforeEnter() {
    this.layout = new LayoutView();
    this.container.show(this.layout);
  },

  routes: {
    'orders'     : 'index',
    'orders/add' : 'add',
    'orders/my'  : 'my',
    'orders/my/:id' : 'myShow',
    'orders/:id/candidates' : 'candidates',
    'orders/:id' : 'show',
  },

  index() {
    return new IndexRoute({
      layout: this.layout
    });
  },

  show() {
    return new ShowRoute({
      layout: this.layout
    });
  },

  add() {
    return new AddRoute({
      layout: this.layout
    });
  },


  my() {
    return new MyRoute({
      layout: this.layout
    });
  },

  myShow() {
    return new MyShowRoute({
      layout: this.layout
    });
  },

  candidates() {
    return new CandidatesRoute({
      layout: this.layout
    });
  },

});
