import nprogress from 'nprogress'
import {ItemView} from 'backbone.marionette'
import FormBehavior from '../../forms/behavior'
import {history} from 'backbone'
import template from './template.hbs'
import storage from '../storage'
import Api from '../../api/api'
import $ from 'jquery'
import nav from '../../nav/nav'
import Dropzone from 'dropzone'

export default ItemView.extend({
  template: template,

  behaviors: {
    form: { behaviorClass: FormBehavior }
  },

  templateHelpers () {
    return {
      errors: this.errors,
      token: Api.token()
    }
  },

  events: {
    'submit form': 'handleSubmit',
    'change #pic': 'upload',
    'change [name=loading_offered]': 'toggleLoadingPrice',
    'click .order__tab': 'switchTab'
  },

  switchTab (e) {

    let type = +$(e.currentTarget).data('value')

    $('.order__tab--active').removeClass('order__tab--active')
    $(e.currentTarget).addClass('order__tab--active')

    this.model.set('type', type)

    if (type === 1) {
      this.showPassengersForm()
    } else {
      this.showCargoForm()
    }
  },

  showPassengersForm () {
    $('.type-cargo').hide()
    $('.type-pass').show()
  },

  showCargoForm () {
    $('.type-cargo').show()
    $('.type-pass').hide()
  },

  upload () {
    var formData = new window.FormData($('form'))
    var file = $('#pic')[0].files[0]

    nprogress.start()
    formData.append('pic', file, 'file.jpg')

    Api
      .upload(formData)
      .then(resp => {
        nprogress.done()
        if (resp.success) {
          $('[name=pics]').val(JSON.stringify([resp.uploaded_pic]))
          $('#preview').attr('src', resp.uploaded_pic)
        }
      })
  },

  toggleLoadingPrice () {
    if ($('[name=loading_offered]').is(':checked')) {
      $('#loading-section').slideDown(200)
    } else {
      $('#loading-section').slideUp(200)
    }
  },

  getRandomInt (min, max) {
    return Math.floor(Math.random() * (max - min)) + min
  },

  handleSubmit () {
    let errors = this.model.validate(this.form)

    if (errors) {
      this.errors = errors
      this.render()
    } else {
      nprogress.start()
      this.model.set(this.form)
      storage.save(this.model).then(() => {
        history.navigate('cars', { trigger: true })
        window.location.reload()
      })
    }
  },

  onShow () {
    nav.do(' ')

    var dropzone = new Dropzone('#dropzone', {
      url: `${Api.baseUrl}/user_file/`,
      paramName: 'pic',
      maxFilesize: 2,
      withCredentials: true,
      success: (file, resp) => {
        let pics = this.model.get('pics')
        if (!pics) {
          pics = [resp.uploaded_pic]
        } else {
          pics.push(resp.uploaded_pic)
        }

        this.model.set('pics', pics)
      }
    })

    dropzone.on('sending', (file, xhr, formData) => {
      formData.append('token', Api.token())
    })

  }
})
