import {ItemView, Renderer} from 'backbone.marionette'
import template from './template.hbs'
import locationTemplate from './location-template.hbs'
import cargoTemplate from './cargo-template.hbs'
import nprogress from 'nprogress'
import $ from 'jquery'
import Pikaday from 'pikaday'
import FormBehavior from '../../forms/behavior'
import storage from '../storage'
import nav from '../../nav/nav'
import _ from 'lodash'
import moment from 'moment'
import Api from '../../api/api'

let maps = window.google.maps

export default ItemView.extend({

  template: template,

  behaviors: {
    form: { behaviorClass: FormBehavior }
  },

  events: {
    'submit form': 'handleSubmit',
    'click .order__tab': 'switchTab',
    'click .address-add': 'addAddress',
    'click .cargo--empty': 'addCargo',
    'click .order-done': 'orderDone',
    'click .save-template': 'templateSave'
  },

  addresses: 0,
  cargos: 0,

  templateHelpers () {

    let date
    let time
    let m = moment.unix(this.model.get('datetime'))

    date = m.format('D MMMM YYYY')
    time = m.format('HH:MM')

    return {
      errors: this.errors,
      date: date,
      time: time
    }
  },

  orderDone () {
    window.location.href = '/#orders'
    window.location.reload()
  },

  templateSave () {
    Api.saveTemplate(this.model.get('id'))
    .then(() => {
      window.location.href = '/#orders'
      window.location.reload()
    })
  },

  onShow () {
    nav.do(' ')

    setTimeout(() => {
      this.addAddress()
      this.addAddress()
      this.addCargo()
    }, 200)

    this.date = new Pikaday({
      field: $('.form-date')[0],
      defaultDate: new Date(),
      format: 'D MMMM YYYY'
    })

    maps.event.addDomListener(window, 'load', this.gmapsInit)
    this.gmapsInit()
  },

  switchTab (e) {

    let type = +$(e.currentTarget).data('value')

    $('.order__tab--active').removeClass('order__tab--active')
    $(e.currentTarget).addClass('order__tab--active')

    this.model.set('type', type)

    if (type === 1) {
      this.showPassengersForm()
    } else {
      this.showCargoForm()
    }
  },

  showPassengersForm () {
    $('.type-cargo').hide()
    $('.type-pass').show()
  },

  showCargoForm () {
    $('.type-cargo').show()
    $('.type-pass').hide()
  },

  addAddress (e) {
    if (e) { e.preventDefault() }

    if (this.addresses > 9) {
      window.alert('   ')
      return
    }

    let $html = this.renderAddr()

    let input = $html.find('.location-addr')[0]

    let autocomplete = new maps.places.Autocomplete(input, {
      types: ['address'],
      componentRestrictions: {country: 'ru'}
    })

    autocomplete.bindTo('bounds', this.map)

    $('#new-addresses').append($html)
    this.addresses++
  },

  resetMarkers () {

    let locations = []

    $('.order__location').each((a, b) => {
      locations.push({
        lat: $(b).find('.location__lat').val(),
        long: $(b).find('.location__long').val()
      })
    })

    let markers = _.map(locations, (loc) => {
      return new maps.Marker({
        position: new maps.LatLng(loc.lat, loc.long),
        map: this.map,
        title: loc.comment
      })
    })

    var bounds = new maps.LatLngBounds()
    _.each(markers, marker => {
      bounds.extend(marker.getPosition())
    })

    this.map.fitBounds(bounds)
  },

  renderAddr () {
    let html = Renderer.render(locationTemplate, {
      number: this.addresses,
      numberPlus: this.addresses + 1
    })

    let $html = $(html)

    $html.find('.toggle').click(e => {
      e.preventDefault()

      let $location = $(e.currentTarget).closest('.order__location')
      $location.find('.additional').slideToggle(200)

      return false
    })

    $html.find('.location-addr').blur(e => {
      let val = e.currentTarget.value
      let $lat = $html.find('.location__lat')
      let $long = $html.find('.location__long')
      this.setCoords(val, $lat, $long)
      this.resetMarkers()
    })

    $html.find('input').keypress(e => {
      if (e.which === 13) {
        e.preventDefault()
        $(e.currentTarget).next().focus()
        return false
      }
    })

    return $html
  },

  addCargo (e) {
    if (e) { e.preventDefault() }

    let html = Renderer.render(cargoTemplate, {number: this.cargos})
    html = $(html)

    $(html).find('.form-dimensions-input').change(e => {
      let $cargo = $(e.currentTarget).closest('.cargo')
      let $dimensions = $cargo.find('.form-dimensions-input')
      let dimensions = _.pluck($dimensions, 'value')
      let $volume = $cargo.find('.cargo-volume')

      var volume = _.reduce(dimensions, (memo, val) => {
        val = Number(val) || 1
        return memo * val
      })

      $volume.val(volume)
    })

    $('#new-cargos').prepend(html)
    $('.cargo--empty').css('height', '503px')

    this.cargos++
  },

  gmapsInit () {
    let mapCanvas = $('#map')[0]

    this.map = new maps.Map(mapCanvas, {
      mapTypeId: maps.MapTypeId.ROADMAP,
      center: new maps.LatLng(55.739356, 37.622882),
      zoom: 11
    })

    this.geocoder = new maps.Geocoder()
  },

  setCoords (address, $lat, $long) {
    this.geocoder.geocode(
      {'address': address},
      (results, status) => {
        if (status !== maps.GeocoderStatus.OK) {
          return
        }

        $lat.val(results[0].geometry.location.lat())
        $long.val(results[0].geometry.location.lng())
      })
  },

  handleSubmit () {

    let date = $('.form-date').val()
    let time = $('.form-time').val()

    $('[name=datetime]').val(moment(`${date} ${time}`).unix())

    let errors = this.model.validate(this.form)

    if (errors) {
      this.errors = errors
      this.render()
    } else {
      nprogress.start()
      this.model.set(this.form)
      storage.save(this.model).then(() => {
        $('.order-success-black, .order-success')
          .fadeIn(200)
      })
    }
  }

})
