import {ItemView} from 'backbone.marionette';
import {history} from 'backbone';
import template from './template.hbs';
import $ from 'jquery';
import moment from 'moment';
import _ from 'lodash';
import Api from '../../api/api';
import user from '../../profile/model';


export default ItemView.extend({
  template: template,

  events: {
    'click .do-accept': 'accept',
    'click .order-done': 'done',
    'click .order-delete': 'delete',
  },

  accept() {
    Api.acceptOrder(this.model.get('id')).then(resp => {
      if (resp.success) {
        history.navigate('orders', {trigger: true});
        location.reload();
      } else {
        window.alert(resp.message);
      }
    });
  },

  done() {
    Api.doneOrder(this.model.get('id')).then(resp => {
      if (resp.success) {
        history.navigate('orders', {trigger: true});
        location.reload();
      }
    });
  },

  delete() {
    Api.cancelOrder(this.model.get('id')).then(resp => {
      if (resp.success) {
        this.model.destroy();
        history.navigate('orders', {trigger: true});
        // location.reload();

        $('.deleted-success').html(`   #${this.model.get('id')}.
          <a href="#"></a>`);
        setTimeout(()=>{
          $('.deleted-success').fadeIn(300);
        }, 400);
        setTimeout(()=>{
          $('.deleted-success').fadeOut(300);
        }, 10000);

      } else {
        window.alert(resp.message);
      }
    });
  },

  initialize(options = {}) {
    this.model = options.model;
    this.user = options.user;
  },

  getStatusText() {
    let status = this.model.get('status');
    let candidates = this.model.get('candidates');

    if (status === 2) {
      return `<span class="text-info"> </span>`;
    }

    if (status === 1 && candidates.length) {
      return `<span class="text-warning">
          :
        ${candidates.length}
      </span>`;
    }

    if (status === 1) {
      return `<span class="text-warning"> </span>`;
    }

    return `<span class="text-success"></span>`;
  },

  templateHelpers() {
    let driver = this.user.isDriver();
    let owner;
    let assignee = this.model.get('assignee_data');
    let price = 0;
    if (assignee) {
      price = this.model.countPrice(assignee.current_car_data);
    }

    if (this.model.get('author') === user.get('id')) {
      owner = true;
    } else {
      owner = false;
    }

    if (price) {
      price = String(price).replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ');
    }

    return {
      driver: driver,
      owner: owner,
      statusText: this.getStatusText(),
      price: price
    };
  },

  gmapsInit() {

    let mapCanvas = $('#orders-map')[0];
    var maps = window.google.maps;

    let locations = this.model.get('locations');

    let map = new maps.Map(mapCanvas, {
      mapTypeId: maps.MapTypeId.ROADMAP
    });

    let markers = _.map(locations, (loc) => {
      return new maps.Marker({
          position: new maps.LatLng(loc.lat, loc.long),
          map: map,
          title: loc.comment
      });
    });


    var bounds = new maps.LatLngBounds();
    _.each(markers, marker => {
      bounds.extend(marker.getPosition());
    });
    map.fitBounds(bounds);
  },

  onShow() {
    window.google.maps.event.addDomListener(window, 'load', this.gmapsInit);
    this.gmapsInit();

    var time = moment.unix( this.model.get('timestamp') );
    $('#order-time').text(time.calendar());

  },

});
