import {ItemView} from 'backbone.marionette';
import template from './template.hbs';
import $ from 'jquery';

export default ItemView.extend({
  template: template,

  events: {
    'click .invite-companies': 'showPopup',
    'click .close-wnd': 'hidePopup'
  },

  showPopup(e) {
    if (e) { e.preventDefault(); }
    $('.order-success').fadeIn(200);
    $('.order-success-black').fadeIn(200);
  },

  hidePopup(e) {
    if (e) { e.preventDefault(); }
    $('.order-success').fadeOut(200);
    $('.order-success-black').fadeOut(200);
  },

  templateHelpers() {
    let firstpic = false;
    let pics = this.model.get('pics');
    let name = this.model.get('name');
    let letter;

    if (name) {
      letter = name[0];
    }

    if (pics && pics.length > 0) {
      firstpic = pics[0];
    }

    return {
      firstpic: firstpic,
      letter: letter
    };
  },

  onShow() {

  },

});
